@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Inter:wght@300;400;500;600;700&display=swap');

body {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 50%, #1a1a1a 100%);
    font-family: 'Inter', sans-serif;
    color: #e5e5e5;
    line-height: 1.6;
}

@layer base {
    img {
        @apply inline-block;
    }

    /* Custom scrollbar */
    ::-webkit-scrollbar {
        width: 8px;
    }

    ::-webkit-scrollbar-track {
        background: #2d2d2d;
    }

    ::-webkit-scrollbar-thumb {
        background: #4a9eff;
        border-radius: 4px;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: #3a8eef;
    }
}

@layer components {
    .gradient-text {
        background: linear-gradient(135deg, #4a9eff 0%, #00d4ff 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .card {
        @apply bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl p-6 shadow-xl;
    }

    .skill-bar {
        @apply bg-white/10 rounded-full h-2 overflow-hidden;
    }

    .skill-progress {
        @apply bg-gradient-to-r from-blue-500 to-cyan-500 h-full rounded-full transition-all duration-1000 ease-out;
    }

    .hover-glow {
        @apply transition-all duration-300 hover:shadow-lg hover:shadow-blue-500/20;
    }
}
